*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #223343;
}
header{
  padding: 50px 15px 0px;
}
header h1{
  color: #fff;
  font-size: 48px;
  font-weight: 500;
  text-align: center;
}
main{
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}
section{
  padding: 15px;
}
.searchbox{
  display: block;
  width: 100%;
  padding: 15px;

  border: none;
  outline: none;
  background: none;
  background-color: #eee;
  color: #53565A;
  font-size: 20px;
  font-weight: 300;
  transition: 0.4s ease-out;
  border-radius: 8px;
}
.searchbox:focus{
box-shadow: 0px 0 8px 3px #4484C4 ;
}
.results{
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}
.result{
  width: 33.33%;
  min-width: 250px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
img{
  display: block;
  width: 100%;
}
h3{
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
  background-color: #445565;
  flex: 1 1 100%;
  transition: 0.4s ease-out;
}
.result:hover h3{
  background-color: #fff;
  color: #223343;
}
.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #445565;
  overflow-y: scroll;
}
.content{
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px;
  color:#fff
}
.content h2{
  font-size: 28px;
  font-weight: 600;
}
span{
  font-weight: 300;
  font-size: 20px;
}
.rating{
  margin-bottom: 30px;
}
.plot{
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px 30px;
}
.plot img{
  flex: 1 1 50px;
  max-width: 300px;
  padding: 0px 15px;
}
.plot p{
flex: 1 1 50%;
padding: 15px;
}
.close{
  display: inline-block;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: 700;
  background-color: #4484C4;
  color: #FFF;
  border: none;
  outline: none;
  appearance: none;
  cursor: pointer;
  transition: 0.4s ease-out;
  border-radius: 8px;
}
.close:hover{
  background-color: #223343;
}